}
.content {
  width: 1100px;
  margin: auto;
  position: relative;
  height: 100%;
}
.left-icon {
  margin-right: 34px;
  font-size: 50px;
  color: #788D94;
}
.description {
  display: inline-block;
  width: 500px;
}
.tilted-bg {
  margin-top: 35px;
  position: relative;
  height: 700px;
  width: 200%;
  background-color: #647481;
  -webkit-transform: perspective( 1300px ) rotateY( 13deg );
          transform: perspective( 1300px ) rotateY( 13deg );
  -webkit-perspective: 300px;
          perspective: 300px;
  z-index: 0;
}
.tilted-bg-parent {
  width: 100%;
  height: auto;
  position: absolute;
}
.section-header {
  font-family: 'ubuntu', helvetica, sans-serif;
  font-weight: 400;
  margin-bottom: 10px;
}
.early-access-parent {
  width: 100%;
  padding: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  background-color: #ECF0F3
}
footer {
  background-color: #FF5050;
  padding: 20px;
  margin-top: 100px;
}


